@import '../../styles/index.scss';

.newpost {
  flex: 1;
  background-color: rgb(211, 240, 224);
  font-family: 'Raleway', sans-serif;

  &__content {
    padding: 40px 0 64px;
    max-width: 600px;
    margin: 0 auto;
    border-radius: 8px;
  }

  &__title {
    font-size: 36px;
    font-weight: 500;
    color: green;
    margin-bottom: 8px;
  }

  &__span {
    color: black;
    font-weight: 400;
    margin-bottom: 24px;
  }

  &__field {
    margin-bottom: 15px;

    input,
    textarea {
      width: 100%;
      padding: 10px;
      border: 1px solid #ccc;
      border-radius: 4px;
      font-size: 16px;
      font-family: 'Raleway', sans-serif;
    }

    textarea {
      height: 120px;
    }

    input:focus,
    textarea:focus {
      border-color: green;
      outline: none;
      box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
    }
  }

  &__textarea {
    width: 100%;
    height: 120px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;

    &:focus {
      border-color: green;
      outline: none;
      box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
    }
  }

  &__input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;

    &:focus {
      border-color: green;
      outline: none;
      box-shadow: 0 0 5px rgba(52, 219, 149, 0.5);
    }
  }
  &__select {
    width: 100%;
    border-radius: 4px;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;

    &:focus {
      border-color: green;
      outline: none;
      box-shadow: 0 0 5px rgba(52, 219, 149, 0.5);
    }
  }

  &__label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: green;
  }

  &__is-danger {
    color: red;
    font-size: 14px;
    margin-top: 5px;
  }

  &__form-actions {
    display: flex;
    gap: 36px;
  }

  &__button {
    width: 120px;
    background-color: green;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 24px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: darken(green, 10%);
    }

    &:disabled {
      background-color: #ccc;
      cursor: not-allowed;
    }
  }

  &__icon {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    height: 16px;
    width: 16px;
    margin-right: 4px;
    display: inline-block;

    &--error {
      background-image: url(../../images/error.svg);
    }
  }

  &__help {
    display: flex;
  }

  &__error {
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    border-radius: 16px;
    height: 64px;
    width: 600px;
    font-size: 16px;
    background-color: rgb(214, 157, 157);
    color: rgb(168, 14, 14);
  }
}
